import React from 'react';
import DivBlock from '../common/DivBlock';

const DotPaintLine = ({
  dotLine,
  dotLineIdx,
  columnCount,
  onMouseDownHandler,
  onMouseUpHandler,
  onMouseOverHandler,
  // onTouchMoveHandler,
}) => {
  function dotMaker() {
    let dotArr = [];
    for (let i = 0; i < columnCount; i++) {
      dotArr.push(
        <DivBlock
          key={i}
          rowIdx={dotLineIdx}
          columnIdx={i}
          onMouseDownHandler={onMouseDownHandler}
          onMouseUpHandler={onMouseUpHandler}
          onMouseOverHandler={onMouseOverHandler}
        />,
      );
    }
    return dotArr;
  }
  return (
    <div>
      {/* {dotLine.map((color, columnIdx) => {
        return (
          <DivBlock
            key={columnIdx}
            rowIdx={dotLineIdx}
            columnIdx={columnIdx}
            dotColor={color}
            onMouseDownHandler={onMouseDownHandler}
            onMouseUpHandler={onMouseUpHandler}
            onMouseOverHandler={onMouseOverHandler}
            // onTouchMoveHandler={onTouchMoveHandler}
          />
        );
      })} */}
      {dotMaker()}
    </div>
  );
};

export default React.memo(DotPaintLine);
